<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>-->
    <router-view />
    <transition name="fade" appear>
      <div class="coming-soon" @click="animate">
        <h1 ref="hk" @mouseover="animate">Coming Soon...</h1>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  created() {
    if(window.location.toString().includes('?wa')){
      window.location = "https://web.whatsapp.com/send?phone=918127987043&text=I am%20interested%20in%20promoting%20our%20brand%20in%20%How%20to%20Start%20a%20Start%20Up%20Event"
    } else {
      window.location = "https://bada-business-ibc.web.app/events/how-to-start-a-startup/sponsor"
    }
  },
  mounted() {
    var el = this.$refs.hk;
    el.innerHTML = el.textContent
      .split("")
      .map((e) => "<span>" + e + "</span>")
      .join("");

    this.animate();
  },
  methods: {
    animate() {
      var el = this.$refs.hk;

      var spans = el.getElementsByTagName("span");
      var i = 0;
      var f = () => {
        if (i < spans.length + 5) {
          setTimeout(f, 50);
        }
        if (i < spans.length) {
          spans[i].style.opacity = 0.4;
        }
        if (i >= 6) {
          spans[i - 6].style.opacity = 1;
        }
        i++;
      };

      setTimeout(f, 50);
    },
  },
};
</script>

